<template>
    <div v-for="(list, index) in lists" class="list">
    <h6>{{ list.name }}</h6>

      <draggable v-model="list.cards" :options="{group: 'cards'}" class="dragArea" @change="cardMoved">
        <div v-for="(card, index) in list.cards" class="card card-body mb-3">
          {{ card.name }}
        </div>
      </draggable>

          <textarea v-model="message" class="form-control mb-1"></textarea>
          <button v-on:click="submitMessage" class="btn btn-sm btn-secondary">Add</button>
    </div>
</template>

<script>
export default {
    props: ["list"],
    data: function () {
     return {
        message: ""
        }

     },
     methods: {
    cardMoved: function(event) {
      console.log('This is cardMoved event: ',event)
      const evt = event.added || event.moved

      if (evt == undefined) { return }

      const element = evt.element
      console.log('This is element: ', element)
      const list_index = window.store.lists.findIndex((list) => {
        return list.cards.find((card) => {
          return card.id === element.id
        })
      })

      var data = new FormData
      // console.log('This is data before: ',data)
      // console.log('This is this.lists: ', this.lists)
      data.append("card[list_id]", window.store.lists[list_index].id)
      data.append("card[position]", evt.newIndex + 1)
      // console.log('This is data after: ',data)
      Rails.ajax({
        url: `/cards/${element.id}/move`,
        type: "PATCH",
        data: data,
        dataType: "json",
      })
    },
     submitMessage: function() {
      var data = new FormData
      data.append("card[list_id]", this.list.id)
      data.append("card[name]", this.message)

      Rails.ajax({
        url: "/cards",
        type: "POST",
        data: data,
        dataType: "json",
        success: (data) => {
          const index = window.store.lists.findIndex(item => item.id == this.list.id)
          // when we drag and drop cards, we need to look up which list has the right id in the lists array
          window.store.lists[index].cards.push(data) // will append item to the proper list
          this.message = undefined
        }
      })
    }
     }
}
</script>

<style scoped>
.dragArea {
  min-height: 20px; 
}
.list {
  background: #E2E4E6;
  border-radius: 3px;
  vertical-align: top;
  margin-right: 10px;
  padding: 10px;
  display: inline-block;
  width: 270px;
}
</style>


